export default {
  name: "ExpressDelivery",
  data() {
    return {
      express: {
        name: "",
        phone: "",
        no: "",
        address:"",
        itemtype:[],
        weight:[],
        cost:0
      },
      thingType: [["", "", "", "", "", ""]],
      weight:[["(5kg)", "(5kg)"]],
      user: {
        isverify: true
      }
    }
  },
  created(){
    this.getUserInfo();
  },
  methods: {
    getUserInfo() {
      this.axios.post("/user/get", {}).then(response => {
        var user = response.data.data;
        this.user = user;
      });
    },
    verify() {
      this.$router.push('/verify');
    }
  }
}



